import IndexGlobal from "../IndexGlobal";
import MgrData from "../mgr/MgrData";
import MgrDataItem from "../mgr/MgrDataItem";
import MCRootStatus from "./MCRootStatus";
import MCRootStatusDrawingBoard from "./MCRootStatusDrawingBoard";
import MCRootStatusExperiment from "./MCRootStatusExperiment";

/**
 * 根状态机
 */
class MCRoot {

    /**
     * 归属的全局变量
     */
    relGlobal: IndexGlobal;

    /**
     * 所有状态的集合
     */
    listStatus = new Array <MCRootStatus> ();
    /**
     * 标识到状态的映射
     */
    mapIdToStatus = new Map <number, MCRootStatus> ();

    constructor (indexGlobal: IndexGlobal) {
        this.relGlobal = indexGlobal;

        this.statusDrawingBoard = new MCRootStatusDrawingBoard (this, 0, `手绘画板`);
        this.statusExperiment = new MCRootStatusExperiment (this, 1, `图片处理`);
    }

    /**
     * 当前状态
     */
    currStatus: MCRootStatus;

    /**
     * 状态 - 画板
     */
    statusDrawingBoard: MCRootStatusDrawingBoard;
    /**
     * 状态 - 实验
     */
    statusExperiment: MCRootStatusExperiment;

    /**
     * 切换状态
     * @param status 
     */
    enter (status: MCRootStatus) {
        let rec = this.currStatus;
        this.currStatus = status;
        if (rec) {
            rec.onExit ();
        };
        this.currStatus.onEnter ();
        MgrData.inst.set (MgrDataItem.MC_STATUS, this.currStatus.id);
    }

    /**
     * 事件派发 - 初始化
     */
    onInit () {
        for (let i = 0; i < this.listStatus.length; i++) {
            let listStatusI = this.listStatus [i];
            listStatusI.onInit ();
        };

        this.enter (this.mapIdToStatus.get (MgrData.inst.get (MgrDataItem.MC_STATUS)));
    }
}

export default MCRoot;